.Testimonials{
    margin: 40px auto 80px;
    width: 80%;
    padding: 0 80px;
    position: relative;
}

.nextBtn, .backBtn{
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    padding: 15px;
    width: 50px;
    border-radius: 50%;
    cursor: pointer;
    background-color: #212EA0;
}

.backBtn{
    right: auto;
    left: 0;
}

.slider{
    overflow: hidden;
}

.slider ul{
    display: flex;
    width: 200%;
    overflow-x: hidden;
    transition: 0.5s;
}

.slider ul li{
    width: 50%;
    padding: 20px;
}

.slide{
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.05);
    padding: 40px;
    border-radius: 10px;
    color: #676767;
    line-height: 1.4;
}

.slide img{
    width: 56px;
    border-radius: 50%;
    margin-right: 10px;
    border: 4px solid #212EA0;
}

.userInfo{
    display: flex;
    align-items: center;
    font-size: 15px;
    margin-bottom: 20px;
}

.userInfo h3{
    color: #212EA0;
}




/* -------------- media query ---------------- */

@media (max-width: 800px) {
    .Testimonials{
        padding: 0 40px;
    }
    .nextBtn, .backBtn{
        width: 35px;
        padding: 10px;
    }
    .Testimonials ul li{
        padding: 10px;
    }
    .slide{
        padding: 10px;
    }
    .slide img{
        width: 50px;
        border: 3px solid #212EA0;
    }
    
}


@media (max-width: 650px) {
    .userInfo{
        display: block;
        margin-bottom: 10px;
        font-size: 12px;
    }
    .userInfo h3{
        font-size: 16px;
        margin-bottom: 6px;
    }
    .slider p{
        font-size: 12px;
    }
    
}